body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  max-width: 600px;
  margin: 50px auto;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.task-form {
  margin-bottom: 20px;
}

.task-form input {
  padding: 10px;
  margin-right: 10px;
  width: 65%;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.task-form button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #28a745;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Botón de agregar */
.task-form button:hover {
  background-color: #218838;
}

.task-form button:active {
  transform: scale(0.95);
}

/* Lista de tareas */
.task-list {
  margin-top: 20px;
}

.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.task-item span {
  cursor: pointer;
}

.task-item.completed span {
  text-decoration: line-through;
  color: #888;
}

/* Botón de eliminar */
.task-item button {
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.task-item button:hover {
  background-color: #c82333;
}

.task-item button:active {
  transform: scale(0.95);
}


